@font-face {
  font-family: 'BogleWeb-Regular';
  src: url("https://moshejs.github.io/walmart-demo/fonts/BogleWeb-Regular.woff2") format("woff2"), url("https://moshejs.github.io/walmart-demo/fonts/BogleWeb-Regular.woff") format("woff"), url("https://moshejs.github.io/walmart-demo/fonts/BogleWeb-Regular.eot") format("embedded-opentype"), url("https://moshejs.github.io/walmart-demo/fonts/BogleWeb-Regular.ttf") format("truetype"); }

@font-face {
