.headerStyle {
  height: 80px;
  background-color: #041E42;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px; }
  @media screen and (min-width: 969px) {
    .headerStyle {
      padding: 0 60px; } }
  .headerStyle nav {
    display: flex; }
    .headerStyle nav a {
      display: none; }
    @media screen and (min-width: 969px) {
      .headerStyle nav {
        justify-content: space-between;
        width: 45%; }
        .headerStyle nav a {
          display: block;
          font-family: 'BogleWeb-Bold';
          font-size: 12px;
          letter-spacing: 1.5px;
          line-height: 30px;
          text-decoration: none;
          text-transform: uppercase;
          color: #FFFFFF; }
          .headerStyle nav a img {
            vertical-align: middle; } }

.hamburgerStyle {
  height: 50%; }
  @media screen and (min-width: 969px) {
    .hamburgerStyle {
      display: none; } }

.logoStyle {
  height: 40%; }
  @media screen and (min-width: 969px) {
    .logoStyle {
      height: 50%; } }

.cart {
  position: relative;
  display: inline-block;
  margin-right: 15px; }
  .cart img {
    width: 115%; }
  .cart .cartQuantity {
    position: absolute;
    right: -15px;
    top: -15px;
    background: #FFC220;
    border-radius: 20px 20px 20px 20px;
    color: black;
    padding: 2px 6px;
    font-size: 11px; }
