.productStyle {
  padding: 15px 10px 20px;
  background-color: #FFFFFF;
  border-radius: 8px;
  box-sizing: border-box; }
  @media screen and (min-width: 969px) {
    .productStyle {
      padding: 20px 30px 20px; } }

.imgStyle {
  width: calc(100% + 20px);
  margin: 0 -0 0 -10px; }
  @media screen and (min-width: 969px) {
    .imgStyle {
      width: calc(100% + 60px);
      margin: 0 -0 0 -30px; } }

.moreBadgeStyle {
  height: 20px;
  border: 2px solid #F6F6F6;
  color: #6D6E71;
  font-size: 12px;
  display: inline-block;
  padding: 0 5px;
  line-height: 20px;
  border-radius: 25px; }

.moreBadgeContainerStyle {
  height: 25px;
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.container {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.itemName {
  font-family: 'BogleWeb-Bold';
  font-size: 14px;
  color: #006697;
  line-height: 28px; }

.itemDescription {
  font-size: 14px;
  color: #041E42;
  width: 55%; }
  @media screen and (min-width: 969px) {
    .itemDescription {
      width: unset; } }

.priceContainer {
  align-self: flex-end;
  justify-self: flex-end; }
  .priceContainer .price {
    font-family: 'BogleWeb-Bold';
    font-size: 11px;
    color: #041E42; }
    @media screen and (min-width: 1200px) {
      .priceContainer .price {
        font-size: 14px; } }
  .priceContainer .discountPrice {
    font-family: 'BogleWeb-Bold';
    font-size: 11px;
    color: #2A8703; }
    @media screen and (min-width: 1200px) {
      .priceContainer .discountPrice {
        font-size: 14px; } }

.originalPrice {
  text-decoration: line-through;
  align-self: center;
  justify-self: flex-end;
  font-size: 11px;
  color: #6D6E71; }
  @media screen and (min-width: 1200px) {
    .originalPrice {
      font-size: 14px; } }
